.pop-up__content {
  width: 430px;
  background: var(--main-text-color);
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (max-width: 470px) {
  .pop-up__content {
    width: 280px;
  }
}